#table-exercise .save-input {
    display: block;
    padding: 8px;
    margin-top: 8px;
}

#table-exercise .cell-edit-field {
    display: block;
    padding: 8px;
}

#table-exercise th, #table-exercise td {
    border: 1px solid var(--mainColor2);
    text-align: left;
    padding: 8px;
}

#table-exercise svg {
    max-height: 30px;
    position: relative;
    left: 5px;
    top: 8px;
}

#table-exercise table, #table-exercise form {
    padding: 20px 20px 0px 20px;
}

#table-exercise td, #table-exercise th {
    cursor: pointer;
}

#table-exercise td.selected {
    background-color: var(--mainColor2);
}